@import url(https://fonts.googleapis.com/css?family=Playfair+Display);
@import url(https://fonts.googleapis.com/css?family=Amatic+SC);
body {
  background-color: #F9F0E2;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: 'Playfair Display';
  width: 100%;
}

.text-container {
  width: 90%;
  height: auto;
  margin: auto;
  margin-bottom: 30px;

  /*border-color: white;*/
  /*border-style: solid;*/
  /*border-width: 2px;*/

  /*text-align: left;*/
}

@media (min-width: 800px) {
  .text-container {
    width: 50%;
  }
}

@media (min-width: 1200px) {
  .text-container {
    width: 40%;
  }
}



.text-container p {
  text-align: left;
}

h1, h2, h3 {
  font-family: Amatic SC;
}

.login {
  margin: 100px auto auto;
  width: 50%;
  display: block;
  text-align: center;
}

.login-button {
  margin-top: 10px;
}

.helper {
  margin-bottom: 1px;
}

.bold-text {
  font-weight: 1000;
}
.header {
    /*padding-bottom: 1rem;*/
    /*width: 75%;*/
    margin-left: auto;
    margin-right: auto;
    background-color: white;
}

.header h1 {
    color: rgb(0, 0, 0);
    margin-bottom: 0;
    margin-top: 0;
}

.header-top {
    display: inline-flex;
}

.header-top img{
    width: auto;
    height: 100px;
    padding: 10px;
}

.headertext{
    margin: auto;
    margin-left: 0;

}

.headertext h1 {
    font-family: "Playfair Display";
}

.headertext2 {
text-align: left;
    margin-top: 10px;

}


.headertext p {
    margin-top: 0;
    color: rgb(0, 0, 0);
    font-style: italic;
}

.navBar {
    display: inline-flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

button {
    margin-bottom: 10px;
    margin-left: 10px;
    margin-right: 10px;
}

.selected {
    background-color: #F9F0E2;
    text-decoration: none;
}

.unselected {
    text-decoration: none;
}
.timer {
    display: inline-flex;
}

.timer-block{
    padding: 10px;
    padding-top: 0;
    padding-bottom: 0;
}
.activity {
    margin: 15px;
}

p .activity{
    margin: 0;
    font-family: "Amatic SC";
    text-align: left;
}


